<template>
<div @click="checkClick" ref="invoiceWrap" class="invoice-wrap flex flex-column">
    <form @submit.prevent="submitForm" class="invoice-content">
        <Loading v-show="loading" />
        <h1 v-if="!editInvoice">New Invoice</h1>
        <h1 v-else>Edit Invoice</h1>
        <!-- Bill From -->
        <div class="bill-from flex flex-column">
            <h4>Bill From</h4>
            <div class="input flex flex-column">
                <label for="billerStreetAddress">Stree Address</label>
                <input required type="text" id="billerStreetAddress" v-model="billerStreetAddress">
            </div>
            <div class="location-details flex ">
                <div class="input flex flex-column">
                    <label for="billerCity">City</label>
                    <input required type="text" id="billerCity" v-model="billerCity">
                </div>
                <div class="input flex flex-column">
                    <label for="billerZipCode">Zip Code</label>
                    <input required type="text" id="billerZipCode" v-model="billerZipCode">
                </div>
                <div class="input flex flex-column">
                    <label for="billerCountry">Country</label>
                    <input required type="text" id="billerCountry" v-model="billerCountry">
                </div>
            </div>
        </div>
        <!-- Bill To -->
        <div class="bill-to flex flex-column">
            <h4>Bill To</h4>
            <div class="input flex flex-column">
                <label for="clientName">Client's Name</label>
                <input required type="text" id="clientName" v-model="clientName">
            </div>
            <div class="input flex flex-column">
                <label for="clientEmail">Client's Email</label>
                <input required type="text" id="clientEmail" v-model="clientEmail">
            </div>
            <div class="input flex flex-column">
                <label for="clientStreetAddress">Stree Address</label>
                <input required type="text" id="clientStreetAddress" v-model="clientStreetAddress">
            </div>

            <div class="location-details flex">
                <div class="input flex flex-column">
                    <label for="clientCity">City</label>
                    <input required type="text" id="clientCity" v-model="clientCity">
                </div>
                <div class="input flex flex-column">
                    <label for="clientZipCode">Zip Code</label>
                    <input required type="text" id="clientZipCode" v-model="clientZipCode">
                </div>
                <div class="input flex flex-column">
                    <label for="clientCountry">Country</label>
                    <input required type="text" id="clientCountry" v-model="clientCountry">
                </div>
            </div>

        </div>
        <!-- Invoice Work Details -->
        <div class="invoice-work flex flex-column">
            <div class="payment flex">
                <div class="input flex flex-column">
                    <label for="invoiceDate">Inoice Date</label>
                    <input disabled required type="text" id="invoiceDate" v-model="invoiceDate">
                </div>

                <div class="input flex flex-column">
                    <label for="paymentDueDate">Payment Due</label>
                    <input disabled required type="text" id="paymentDueDate" v-model="paymentDueDate">
                </div>
            </div>
            <div class="input flex flex-column">
                <label for="paymentTerms">Payment Terms</label>
                <select required type="text" id="paymentTerms" v-model="paymentTerms">
                    <option value="30">Net 30 Days</option>
                    <option value="60">Net 60 Days</option>
                </select>
            </div>
            <div class="input flex flex-column">
                <label for="productDescription">Product Description</label>
                <input required type="text" id="productDescription" v-model="productDescription">
            </div>
            <div class="work-items">
                <h3>Item List</h3>
                <table class="item-list">
                    <tr class="table-heading flex">
                        <th class="item-name">Item Name</th>
                        <th class="qty">Qty</th>
                        <th class="price">Price</th>
                        <th class="total">Total</th>
                    </tr>
                    <tr class="table-items flex" v-for="(item, i) in invoiceItemList" :key="i">
                        <td class="item-name"><input type="text" v-model="item.itemName"></td>
                        <td class="qty"><input type="text" v-model="item.qty"></td>
                        <td class="price"><input type="text" v-model="item.price"></td>
                        <td class="total">${{item.total = item.qty * item.price}}</td>
                        <img style="cursor: pointer" @click="deleteInvoiceItem(item.id)" src="@/assets/icon-delete.svg" alt="">
                    </tr>
                </table>
                <div @click="addNewInvoiceItem" class="flex button">
                    <img src="@/assets/icon-plus.svg" alt="">
                    Add New Item
                </div>
            </div>
        </div>
        <!-- Save/Exit -->
        <div class="save flex">
            <div class="left">
                <button type="button" @click="closeInvoice" class="red">Cancel</button>
            </div>
            <div class="right flex">
                <button type="submit" v-if="!editInvoice" @click="saveDraft" class="dark-purple">Save Draft</button>
                <button type="submit" v-if="!editInvoice" @click="publishInvoice" class="purple">Create Invoice</button>
                <button type="submit" v-if="editInvoice" class="purple">Update Invoice</button>
            </div>
        </div>
    </form>
</div>
</template>

<script>
import db from '../firebase/firebaseInit';
import {
    collection,
    updateDoc,
    doc,
    setDoc
} from 'firebase/firestore/lite'
import Loading from '../components/Loading.vue';
import {
    mapMutations,
    mapState,
    mapActions
} from 'vuex';
import {
    uid
} from 'uid';
import Swal from 'sweetalert2';
export default {
    name: 'invoiceModal',
    components: {
        Loading
    },
    data() {

        return {
            loading: null,
            dateOptions: {
                year: "numeric",
                month: "short",
                day: "numeric"
            },
            invoiceId: null,
            billerStreetAddress: null,
            billerCity: null,
            billerZipCode: null,
            billerCountry: null,
            clientName: null,
            clientEmail: null,
            clientStreetAddress: null,
            clientCity: null,
            clientZipCode: null,
            clientCountry: null,
            invoiceDateUnix: null,
            invoiceDate: null,
            paymentTerms: null,
            paymentDueDateUnix: null,
            paymentDueDate: null,
            productDescription: null,
            invoicePending: null,
            invoiceDraft: null,
            invoiceItemList: [],
            invoiceTotal: 0,
        }
    },

    computed: {
        ...mapState(['editInvoice', 'currentInvoiceArray'])
    },

    watch: {
        paymentTerms() {
            const futureDate = new Date();
            this.paymentDueDateUnix = futureDate.setDate(futureDate.getDate() + parseInt(this.paymentTerms));
            this.paymentDueDate = new Date(this.paymentDueDateUnix).toLocaleDateString("en-us", this.dateOptions)
        }
    },

    created() {
        //get current date for invoice date
        if (!this.editInvoice) {
            this.invoiceDateUnix = Date.now();
            this.invoiceDate = new Date(this.invoiceDateUnix).toLocaleDateString("en-us", this.dateOptions);
        }

        if (this.editInvoice) {
            const invoice = this.currentInvoiceArray[0];

            this.invoiceId = invoice.invoiceId;
            this.billerStreetAddress = invoice.billerStreetAddress;
            this.billerCity = invoice.billerCity;
            this.billerZipCode = invoice.billerZipCode;
            this.billerCountry = invoice.billerCountry;
            this.clientName = invoice.clientName;
            this.clientEmail = invoice.clientEmail;
            this.clientStreetAddress = invoice.clientStreetAddress;
            this.clientCity = invoice.clientCity;
            this.clientZipCode = invoice.clientZipCode;
            this.clientCountry = invoice.clientCountry;
            this.invoiceDate = invoice.invoiceDate;
            this.invoiceDateUnix = invoice.invoiceDateUnix;
            this.paymentTerms = invoice.paymentTerms;
            this.paymentDueDateUnix = invoice.paymentDueDateUnix;
            this.paymentDueDate = invoice.paymentDueDate;
            this.productDescription = invoice.productDescription;
            this.invoiceItemList = invoice.invoiceItemList;
            this.invoiceTotal = invoice.invoiceTotal;
            this.invoicePending = invoice.invoicePending;
            this.invoiceDraft = invoice.invoiceDraft;
            this.invoicePaid = invoice.invoicePaid;
        }
    },
    methods: {
        ...mapMutations(['TOGGLE_INVOICE', 'TOGGLE_MODAL', 'TOGGLE_EDIT_INVOICE', 'SET_CURRENT_INVOICE']),
        ...mapActions(['UPDATE_INVOICE', 'GET_INVOICES']),
        checkClick(e) {
            if (e.target === this.$refs.invoiceWrap) {
                this.TOGGLE_MODAL();
            }
        },
        closeInvoice() {
            this.TOGGLE_INVOICE();
            if (this.editInvoice) {
                this.TOGGLE_EDIT_INVOICE();
            }
        },

        addNewInvoiceItem() {
            this.invoiceItemList.push({
                id: uid(),
                itemName: "",
                price: 0,
                qty: "",
                total: 0
            })
        },

        deleteInvoiceItem(id) {
            this.invoiceItemList = this.invoiceItemList.filter(el => el.id !== id);
            // const index = this.invoiceItemList.findIndex(el => el.id == id);
            // this.invoiceItemList.splice(index, 1);
        },

        publishInvoice() {
            this.invoicePending = true;
        },

        saveDraft() {
            this.invoiceDraft = true;
        },

        submitForm() {
            if (this.editInvoice) {
                this.updateInvoice();
                return;
            }

            this.uploadInvoice();
        },

        calcInvoiceTotal(Invoice) {
            this.invoiceTotal = 0;
            const total = Invoice.reduce((acc, val) => acc + val.total, 0);
            return total;
        },

        async uploadInvoice() {
            try {
                if (this.invoiceItemList.length <= 0) {
                    Swal.fire({
                        icon: 'info',
                        text: "Please ensure you filled out work items",
                        title: 'Oops..'
                    })
                    return
                }

                this.invoiceTotal = this.calcInvoiceTotal(this.invoiceItemList);
                this.loading = true;
                const id = uid();
                await setDoc(doc(db, "invoices", id), {
                    invoiceId: id,
                    billerStreetAddress: this.billerStreetAddress,
                    billerCity: this.billerCity,
                    billerZipCode: this.billerZipCode,
                    billerCountry: this.billerCountry,
                    clientName: this.clientName,
                    clientEmail: this.clientEmail,
                    clientStreetAddress: this.clientStreetAddress,
                    clientCity: this.clientCity,
                    clientZipCode: this.clientZipCode,
                    clientCountry: this.clientCountry,
                    invoiceDate: this.invoiceDate,
                    invoiceDateUnix: this.invoiceDateUnix,
                    paymentTerms: this.paymentTerms,
                    paymentDueDateUnix: this.paymentDueDateUnix,
                    paymentDueDate: this.paymentDueDate,
                    productDescription: this.productDescription,
                    invoiceItemList: this.invoiceItemList,
                    invoiceTotal: this.invoiceTotal,
                    invoicePending: this.invoicePending,
                    invoiceDraft: this.invoiceDraft,
                });

                this.loading = false;
                // await database.set();
                this.TOGGLE_INVOICE();
                // this.SET_CURRENT_INVOICE([])
                this.GET_INVOICES();

            } catch (error) {
                console.log(error);
            }
        },
        async updateInvoice() { 
            try {
                if (this.invoiceItemList.length <= 0) {
                    Swal.fire({
                        icon: 'info',
                        text: "Please ensure you filled out work items",
                        title: 'Oops..'
                    })
                    return
                }

                this.invoiceTotal = this.calcInvoiceTotal(this.invoiceItemList);
                this.loading = true;
                console.log('ID',this.invoiceId);
                await updateDoc(doc(db, "invoices", this.invoiceId), {
                    billerStreetAddress: this.billerStreetAddress,
                    billerCity: this.billerCity,
                    billerZipCode: this.billerZipCode,
                    billerCountry: this.billerCountry,
                    clientName: this.clientName,
                    clientEmail: this.clientEmail,
                    clientStreetAddress: this.clientStreetAddress,
                    clientCity: this.clientCity,
                    clientZipCode: this.clientZipCode,
                    clientCountry: this.clientCountry,
                    invoiceDate: this.invoiceDate,
                    invoiceDateUnix: this.invoiceDateUnix,
                    paymentTerms: this.paymentTerms,
                    paymentDueDateUnix: this.paymentDueDateUnix,
                    paymentDueDate: this.paymentDueDate,
                    productDescription: this.productDescription,
                    invoiceItemList: this.invoiceItemList,
                    invoiceTotal: this.invoiceTotal,

                });

                this.loading = false;
                // await database.set();
                const data = {
                    invoiceId: this.invoiceId,
                    routeId: this.$route.params.invoiceId
                };

                console.log(data);
                this.UPDATE_INVOICE(data);

            } catch (error) {
                console.log(error);
            }
        },
    }
}
</script>

<style lang="scss" scoped>
.invoice-wrap {
    position: fixed;
    top: 0;
    left: 0;
    background-color: transparent;
    width: 100%;
    height: 100vh;
    overflow: scroll;

    &::-webkit-scrollbar {
        display: none;
    }

    & {
        -ms-overflow-style: none;
        /* IE and Edge */
        scrollbar-width: none;
        /* Firefox */
    }

    @media (min-width: 900px) {
        left: 90px;
    }

    .invoice-content {
        position: relative;
        padding: 56px;
        max-width: 750px;
        width: 100%;
        background-color: #141625;
        color: #fff;
        box-shadow: 10px 4px 6px -1px rgba(0, 0, 0, 0.2), 0 2px 4px -1px rgba(0, 0, 0, 0.06);

        h1 {
            margin-bottom: 48px;
            color: #fff;

        }

        h3 {
            margin-bottom: 16px;
            font-size: 18px;
            color: #777f98;
        }

        h4 {
            color: #7c5dfa;
            font-size: 12px;
            margin-bottom: 24px;
        }

        //Bill To and Bill from
        .bill-to,
        .bill-from {
            margin-bottom: 48px;

            .location-details {
                gap: 16px;

                div {
                    flex: 1;
                }
            }
        }

        //INvoice work
        .invoice-work {
            .payment {
                gap: 24px;

                div {
                    flex: 1;
                }
            }

            .work-items {
                .item-list {
                    width: 100%;

                    //Item Table Styling
                    .table-heading,
                    .table-items {
                        gap: 16px;
                        font-size: 12px;

                        .item-name {
                            flex-basis: 50%;
                        }

                        .qty {
                            flex-basis: 10%;
                        }

                        .price {
                            flex-basis: 20%;
                        }

                        .total {
                            flex-basis: 20%;
                            align-self: center;
                        }
                    }

                    .table-heading {
                        margin-bottom: 16px;

                        th {
                            text-align: left;
                        }
                    }

                    .table-items {
                        position: relative;
                        margin-bottom: 24px;

                        img {
                            position: absolute;
                            top: 15px;
                            right: 0;
                            width: 12px;
                            height: 16px;
                        }
                    }
                }

                .button {
                    color: #fff;
                    background-color: #252945;
                    align-items: center;
                    justify-content: center;
                    margin: 16px 0;
                    width: 100%;

                    img {
                        margin-right: 4px;
                    }
                }
            }

        }

        .save {
            div {
                flex: 1
            }

            .right {
                justify-content: flex-end;
            }
        }
    }

    .input {
        margin-bottom: 24px;
    }

    label {
        font-size: 13px;
        margin-bottom: 6px;
    }

    input,
    select {
        width: 100%;
        background-color: #1e2139;
        color: #fff;
        border-radius: 4px;
        padding: 12px 6px;
        border: none;

        &:focus {
            outline: none;
        }
    }

}
</style>
